.header {
  position: relative;
  padding-top: 7px;
  background-color: var(--secondary-bg);
  z-index: -2;
}

.header__background {
  position: absolute;
  bottom: 0;
  transform: translateY(410px);

  left: 0;
  right: 0;
  height: 460px;
  background-image: url(../images/background/bgtop-mob.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  z-index: -2;
}
