.header__logo-box {
  width: calc(96 / 320 * 100vw);
  height: calc(43 / 320 * 100vw);
  margin-left: auto;
  margin-right: auto;
  background-repeat: no-repeat;
  background-size: cover;
}

.header__image-box {
  top: 0;
  left: 0;
  position: absolute;
  width: calc(117 / 320 * 100vw);
  height: calc(113 / 320 * 100vw);
  background-image: url(../images/png/mobile/header-donuts_mobile_2x.png);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
  @include retina2 {
    background-image: url(../images/png/mobile/header-donuts_mobile_4x.png);
  }
}
